
























import {Component, Vue, Watch} from 'vue-property-decorator';
import ProjectRepository from '@/modules/project/repository/project-repository';
import Profile from '@/modules/users/models/Profile';
import Project from '@/modules/project/models/project';
import CreateProjectStep4 from '@/modules/project/components/create-project-steps/CreateProjectStep4.vue';

@Component({
  components: {
    CreateProjectStep4,
  },
  notifications: {
    showError: {
      title: 'Error',
      message: 'Update Seting Failed',
      type: 'error',
    },
    showSuccess: {
      title: 'Success',
      message: 'Update Seting Success',
      type: 'success',
    },
  },
} as any)
export default class SettingTab extends Vue {
  project: Project | null = null;
  loading: boolean = false;
  updateLoading: boolean = false;

  get projectId() {
    return (this.$route.params as any).projectId;
  }

  @Watch('projectId', {immediate: true})
  async onProjectIdChange() {
    this.loading = true;
    this.project = await ProjectRepository.getProjectById(this.projectId);
    this.loading = false;
  }

  async addUser(user: Profile) {
    const result = await ProjectRepository.addOwner(this.projectId, user.userId, 'Owner');
    if (result) {
      await this.reloadModel();
    }
  }

  async updateProjectSetting() {
    this.updateLoading = true;
    if (this.project) {
      const result = await ProjectRepository.updateProject(this.project);
      if (result) {
        (this as any).showSuccess();
      } else {
        (this as any).showError();
      }
    }
    this.updateLoading = false;
  }

  async reloadModel() {
    await this.onProjectIdChange();
  }
}
