









































import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import Project from '@/modules/project/models/project';
import ProjectSaleProgress from '@/modules/project/components/ProjectSaleProgress.vue';

@Component({
  components: {
    ProjectSaleProgress,
  },
})
export default class ProjectItem extends Vue {
  @Prop() project!: Project;

  get projectBanner() {
    switch (this.project.projectInfo.categoryType) {
      case 'Art':
        return require('@/assets/ART.jpeg');
      case 'Crypto Assets':
        return require('@/assets/CRYPTO-ASSETS.jpeg');
      case 'Hedge Funds':
        return require('@/assets/HEDGE-FUNDS.jpeg');
      case 'Infrastructure':
        return require('@/assets/INFRASTRUCTURE.jpeg');
      case 'Natural Resources':
        return require('@/assets/NATURAL-RESOURCES.jpeg');
      case 'Private Debt':
        return require('@/assets/Private-Debt.jpg');
      case 'Private Equity':
        return require('@/assets/PRIVATE-EQUITY.jpeg');
      case 'Real Estate':
        return require('@/assets/REAL-ESTATE.jpeg');
      case 'Venture Capital':
        return require('@/assets/Venture-Capital.jpg');
      case 'Other':
        return require('@/assets/Other.jpg');
    }
  }

  goToProjectDetailScreen() {
    this.$router.push({name: 'project-detail', params: {projectId: this.project!.id}});
  }
}
