
























































import {Component, Prop, Vue} from 'vue-property-decorator';
import ACategoryTypeSelect from '@/components/ACategoryTypeSelect.vue';
import ACountrySelect from '@/components/ACountrySelect.vue';
import AIndustrySelect from '@/components/AIndustrySelect.vue';
import ARegionSelect from '@/components/ARegionSelect.vue';
import {EProjectType, IProjectFilter} from '@/modules/project/models/project';

@Component({
  components: {
    ACategoryTypeSelect,
    ACountrySelect,
    ARegionSelect,
    AIndustrySelect,
  },
})
export default class ProjectFilter extends Vue {
  @Prop() value!: IProjectFilter;
  @Prop({default: false, type: Boolean}) noStatus!: string;
  types: string[] = [EProjectType.FUNDRAISING, EProjectType.ASSET];

  mounted() {
    this.value.type = EProjectType.FUNDRAISING;
  }

  get type(): string {
    return this.value.type;
  }

  onSelectStatus(type: EProjectType) {
    this.value.type = type;
  }
}
