



















































































import {Component, Prop, Vue, Watch} from 'vue-property-decorator';
import Project, {IOwner} from '@/modules/project/models/project';
import UserRepository from '@/modules/users/repository/user-repository';
import ProjectAddUser from '@/modules/project/components/ProjectAddUser.vue';
import Profile from '@/modules/users/models/Profile';


@Component({
  components: {
    ProjectAddUser,
  },
})
export default class CreateProjectStep4 extends Vue {
  @Prop() loading!: boolean;
  @Prop() value!: Project;
  @Prop({type: Boolean, default: false}) noMemberManager: boolean;

  editorOption: any = {};
  owners: any = [];

  kycLevel = [
    {
      text: 'Level 0',
      value: 0,
      hint: '(require Email - Investors must verify the email before they can invest in this project)',
    },
    {
      text: 'Level 1',
      value: 1,
      hint: '(require KYC - Investors must verify the KYC before they can invest in this project)',
    },
  ];

  transactionRequired = [
    {
      value: true,
      text: 'Yes',
      hint: '(Investors must have permission from managers to buy and sell units)',
    },
    {
      value: false,
      text: 'No',
      hint: '(Investors can freely buy and sell units without the permission of managers)',
    },
  ];

  @Watch('value.owners.length')
  onValueChanged() {
    if (this.value) {
      const promises: any = [];

      this.value.owners.forEach((owner: IOwner) => {
        promises.push(UserRepository.getProfileByUserId(owner.id));
      });

      Promise.all(promises).then((result: any) => {
        this.owners = result.map((user: Profile) => {
          return {
            id: user.userId,
            email: user.email,
            title: 'Owner',
          };
        });
      });
    }
  }

  goBackAction() {
    this.$emit('back');
  }

  goToNextAction() {
    this.$emit('next');
  }

  addUser(user: Profile) {
    this.$emit('add-owner', user);
  }

}
