import * as Vue from 'vue';
import $ from 'jquery';

const ns = '_nano_scroller';

const updateNano = (binding: Vue.VNodeDirective, el: HTMLElement) => {
  if (binding.value !== binding.oldValue) {
    if (binding.value) {
      ($(el) as any).nanoScroller();
    } else {
      ($(el) as any).nanoScroller({destroy: true});
    }
  }
};

const DirectiveOptions: Vue.DirectiveOptions = {
  bind(el: HTMLElement,
       binding: Vue.VNodeDirective,
       vnode: Vue.VNode,
       oldVnode: Vue.VNode) {
    updateNano(binding, el);
  },
  componentUpdated(el: HTMLElement,
                   binding: Vue.VNodeDirective,
                   vnode: Vue.VNode,
                   oldVnode: Vue.VNode) {
    updateNano(binding, el);
  },
  unbind(el: any, binding: any, vnode: any) {
    ($(el) as any).nanoScroller({destroy: true});
  },
};

export default DirectiveOptions;
