











































import {Component, Vue, Watch} from 'vue-property-decorator';
import ExchangeTransaction from '../../models/exchange-transaction';
import ExchangeTransactionRepository from '@/modules/exchange/repository/exchange-transaction-repository';
import ProjectFilter from '@/modules/project/components/ProjectFilter.vue';
import * as firebase from 'firebase';
import Project, {EProjectType, IProjectFilter} from '@/modules/project/models/project';
import ProjectRepository from '@/modules/project/repository/project-repository';

@Component({
  components: {
    ProjectFilter,
  },
})
export default class ExchangeBuyerList extends Vue {
  headers = [
    {text: 'Created Time', align: 'center', sortable: false, value: 'time'},
    {text: 'Buyer', align: 'center', sortable: false, value: 'seller'},
    {text: 'Asset', align: 'center', sortable: false, value: 'name'},
    {text: 'Quantity', value: 'amount', align: 'right', sortable: false},
    {text: 'Price', align: 'right', sortable: false},
    {text: 'Expiration Time', align: 'center', sortable: false, value: 'time'},
    {text: 'Actions', align: 'center', sortable: false},
  ];
  filter: IProjectFilter = {
    status: 'ALL',
    category: '',
    country: '',
    searchText: '',
    type: EProjectType.FUNDRAISING,
    geographic: '',
    industry: '',
  };
  loading = true;
  transactions: ExchangeTransaction[] = [];
  allTransactions: ExchangeTransaction[] = [];
  projects: Project[] = [];

  mounted() {
    this.fetchData();
  }

  submit(id: string) {
    this.$router.push({name: 'process-exchange', params: {transactionId: id}});
  }

  get canSell() {
    return firebase.auth().currentUser;
  }

  @Watch('filter', {deep: true, immediate: true})
  async refetchFilterData() {
    this.loading = true;
    this.projects = await ProjectRepository.getProjects(this.filter);
    this.transactions = [];
    try {
      this.allTransactions.forEach((item) => {
        this.projects.forEach((project) => {
          if (project.id === item.projectId) {
            this.transactions.push(item);
          }
        });
      });
    } catch (e) {
    } finally {
      this.loading = false;
    }
  }

  async fetchData() {
    this.loading = true;
    try {
      this.allTransactions = await ExchangeTransactionRepository.getRunningTransactions('BUY');
      this.transactions = this.allTransactions;
    } catch (e) {
    } finally {
      this.loading = false;
    }
  }

  isOwnerUser(id: string) {
    return this.$store.state.user.user.id === id;
  }
}
