import {db} from '@/setup/firebase';
import ExchangeTransaction, {ExchangeType} from '@/modules/exchange/models/exchange-transaction';
import * as firebase from 'firebase';
// @ts-ignore
import {IProjectFilter} from '@/modules/project/components/ProjectFilter.vue';
import Project from '@/modules/project/models/project';

export enum ETransactionStatus {
  RUNNING = 'running',
  PENDING = 'pending',
  SUCCESS = 'success',
  REJECT = 'reject',
  RETURN = 'return',
}

export default class ExchangeTransactionRepository {
  public static async createTransaction(transaction: ExchangeTransaction): Promise<boolean> {
    try {
      const {id, ...data} = transaction.getData() as any;
      const projectDocument = await db.collection('exchange-transactions').add(data);
      if (projectDocument) {
        return true;
      }
    } catch (e) {
      return false;
    }

    return false;
  }

  public static async getRunningTransactions(type: ExchangeType): Promise<ExchangeTransaction[]> {
    const transactions: ExchangeTransaction[] = [];

    const querySnapshot = await db.collection('exchange-transactions')
      .where('status', '==', 'running')
      .where('type', '==', type)
      .get();

    querySnapshot.forEach((doc) => {
      const transaction = new ExchangeTransaction(doc.data(), doc.id);

      transactions.push(transaction);
    });

    return transactions;
  }

  public static async getPendingTransactions(projects?: Project[]): Promise<ExchangeTransaction[]> {
    let transactions: ExchangeTransaction[] = [];

    const querySnapshot = await db.collection('exchange-transactions')
      .where('status', '==', 'pending').get();

    querySnapshot.forEach(async (doc) => {
      const data: any = doc.data();

      const transaction = new ExchangeTransaction(data, doc.id);

      transactions.push(transaction);
    });

    if (projects) {
      transactions = transactions.filter((transaction: ExchangeTransaction) =>
        projects.some((project: Project) =>
          project.id === transaction.projectId));

    }
    return transactions;
  }


  public static async getMyHistoryTransactions(status: ETransactionStatus = ETransactionStatus.SUCCESS):
    Promise<ExchangeTransaction[]> {
    const transactions: ExchangeTransaction[] = [];

    const userId = firebase.auth().currentUser!.uid;

    let querySnapshot = await db.collection('exchange-transactions')
      .where('fromUserId', '==', userId)
      .where('status', '==', status)
      .get();

    querySnapshot.forEach((doc) => {
      const transaction = new ExchangeTransaction(doc.data(), doc.id);

      transactions.push(transaction);
    });

    querySnapshot = await db.collection('exchange-transactions')
      .where('toUserId', '==', userId)
      .where('status', '==', status)
      .get();

    querySnapshot.forEach((doc) => {
      const transaction = new ExchangeTransaction(doc.data(), doc.id);

      transactions.push(transaction);
    });

    return transactions;
  }

  public static async getTransactionById(id: string): Promise<ExchangeTransaction | null> {
    let transaction: ExchangeTransaction | null = null;

    const querySnapshot = await db.collection('exchange-transactions')
      .doc(id).get();

    transaction = new ExchangeTransaction(querySnapshot.data(), querySnapshot.id);

    return transaction;
  }

  public static async processBuyTransaction(id: string, fromUserId: string, status: string): Promise<boolean> {

    await db.collection('exchange-transactions')
      .doc(id).update({
        fromUserId,
        status,
      });

    return true;
  }

  public static async processSellTransaction(id: string, toUserId: string, status: string): Promise<boolean> {
    await db.collection('exchange-transactions')
      .doc(id).update({
        toUserId,
        status,
      });

    return true;
  }

  public static async updateTransactionStatus(transactionId: string, status: ETransactionStatus):
    Promise<boolean> {
    await db.collection('exchange-transactions')
      .doc(transactionId).update({
        status,
      });

    return true;
  }

  public static async updateTransactionSeller(transactionId: string, fromUserId: string): Promise<boolean> {
    await db.collection('exchange-transactions')
      .doc(transactionId).update({
        fromUserId,
      });

    return true;
  }

  public static async updateTransactionBuyer(transactionId: string, toUserId: string): Promise<boolean> {
    await db.collection('exchange-transactions')
      .doc(transactionId).update({
        toUserId,
      });

    return true;
  }

  public static async rejectTransaction(id: string): Promise<boolean> {
    await db.collection('exchange-transactions')
      .doc(id).update({
        status: 'reject',
      });

    return true;
  }
}
