




















































































import {Component, Prop, Vue} from 'vue-property-decorator';
import ExchangeTransaction from '@/modules/exchange/models/exchange-transaction';
import * as firebase from 'firebase';
import ExchangeLogic from '@/modules/exchange/exchange-logic';

@Component({})
export default class ProcessTransactionStep4 extends Vue {
  @Prop() value!: ExchangeTransaction;
  @Prop({type: Boolean, default: false}) isOwner: boolean;
  image: string = '';
  acceptLoading: boolean = false;
  rejectLoading: boolean = false;
  returnLoading: boolean = false;

  headers = [
    {text: 'Time', align: 'center', sortable: false, value: 'time'},
    {text: 'Seller', align: 'center', sortable: false, value: 'seller'},
    {text: 'Buyer', align: 'center', sortable: false, value: 'buyer'},
    {text: 'Asset', align: 'center', sortable: false, value: 'name'},
    {text: 'Quantity', value: 'amount', align: 'right', sortable: false},
    {text: 'Price', align: 'right', sortable: false},
  ];


  async mounted() {
    this.image = await firebase.storage().ref('document').child(this.value.id).getDownloadURL();
  }

  get items() {
    return [
      {
        amount: this.value.tokenAmount,
        price: this.value.price,
        seller: (this.value as any).seller,
        buyer: (this.value as any).buyer,
        name: this.value.tokenName,
        time: this.value.createdTime,
        status: this.value.status,
      },
    ];
  }

  reloadModel() {
    this.$emit('reload-model');
  }

  get projectId() {
    return this.value.projectId;
  }

  async acceptTransaction(item: ExchangeTransaction) {
    this.acceptLoading = true;
    if (item.type === 'BUY') {
      const result = await ExchangeLogic.processBuyTransaction(item, this.projectId);
    } else {
      const result = await ExchangeLogic.processSellTransaction(item, this.projectId);
    }
    this.acceptLoading = false;
    this.reloadModel();
  }

  async rejectTransaction(item: ExchangeTransaction) {
    this.rejectLoading = true;
    const result = await ExchangeLogic.processRejectTransaction(item, this.projectId);
    this.rejectLoading = false;
    this.reloadModel();
  }

  async returnTransaction(item: ExchangeTransaction) {
    this.returnLoading = true;
    const result = await ExchangeLogic.processReturnTransaction(item);
    this.returnLoading = false;
    this.reloadModel();
  }
}
