import * as Vue from 'vue';

const ns = '_click_outside';

class ClickOutsideHandler {
  protected ev: EventListener;
  protected installed: boolean = false;
  protected el: HTMLElement;
  protected startTime: number;

  constructor(el: HTMLElement, binding: Vue.VNodeDirective) {
    this.ev = (e: Event) => {
      const isChild = el.contains(e.target as HTMLElement);
      if ((binding.modifiers as any).delay) {
        if (new Date().getTime() - this.startTime >= 300 && e.target !== el && !isChild && binding.value) {
          binding.value();
        }
      } else {
        if (e.target !== el && !isChild && binding.value) {
          binding.value();
        }
      }
    };
    this.el = el;
    this.update();
  }

  elementVisible() {
    return document.body.contains(this.el) && this.el.style.display !== 'none';
  }

  update() {
    if (this.installed) {
      if (!this.elementVisible()) {
        document.body.removeEventListener('click', this.ev, false);
        this.installed = false;
      }
    } else {
      if (this.elementVisible()) {
        this.installed = true;
        // Push this code block to the queue
        setTimeout(() => {
          if (this.elementVisible()) {
            this.startTime = new Date().getTime();
            document.body.addEventListener('click', this.ev, false);
          }
        });
      }
    }
  }

  destroy() {
    document.body.removeEventListener('click', this.ev, false);
  }
}

const DirectiveOptions: Vue.DirectiveOptions = {
  bind(el: HTMLElement,
       binding: Vue.VNodeDirective,
       vnode: Vue.VNode,
       oldVnode: Vue.VNode) {
    (el as any)[ns] = new ClickOutsideHandler(el, binding);
  },
  componentUpdated(el: HTMLElement,
                   binding: Vue.VNodeDirective,
                   vnode: Vue.VNode,
                   oldVnode: Vue.VNode) {
    if (ns in el) {
      (el as any)[ns].update();
    }
  },
  unbind(el, binding, vnode) {
    if (ns in el) {
      (el as any)[ns].destroy();
    }
  },
};

export default DirectiveOptions;
