


























































import {Component, Vue} from 'vue-property-decorator';
import ANotificationList from './ANotificationList.vue';
import ProjectRepository from '@/modules/project/repository/project-repository';
import Project, {IOwner} from '@/modules/project/models/project';
import ExchangeTransactionRepository, {ETransactionStatus} from '@/modules/exchange/repository/exchange-transaction-repository';
import ExchangeTransaction from '@/modules/exchange/models/exchange-transaction';
import * as firebase from 'firebase';

export interface INotificationItem {
  logo: string;
  title: string;
  body: string;
  time: string;
  id: string;
}

@Component({
  components: {
    ANotificationList,
  },
})
export default class ANotificationListButton extends Vue {
  focus: boolean = false;
  notifications: any = [];
  transactions: ExchangeTransaction[] = [];

  defaultLogo: any = require('@/assets/avatar-placeholder.png');

  async mounted() {
    const projects = await ProjectRepository.getProjects();
    const userId = firebase.auth().currentUser!.uid;

    if (projects && userId) {
      const myProjects = projects.filter((project: Project) => project.owners.some((owner: IOwner) => owner.id === userId));
      const projectTransaction = await ExchangeTransactionRepository.getPendingTransactions(myProjects);
      const myPendingTransactions = await ExchangeTransactionRepository.getMyHistoryTransactions(ETransactionStatus.PENDING);
      this.transactions = [...projectTransaction, ...myPendingTransactions];
    }
  }

  get messages() {
    return this.transactions.map((transaction: ExchangeTransaction) => {
      return {
        logo: this.defaultLogo,
        title: `Pending transaction for ${transaction.tokenName}`,
        body: `${transaction.tokenAmount} ${transaction.tokenName} x ${transaction.price} USD = ${transaction.total} USD`,
        time: transaction.createdTime,
        id: transaction.id,
      };
    });
  }

  get pendingTask() {
    return this.messages.length;
  }

  hide() {
    this.focus = false;
  }

  onButtonClick() {
    this.focus = !this.focus;
  }

  onItemClicked(item: INotificationItem) {
    this.$router.push({name: 'transaction-detail', params: {id: item.id}});
  }

}

